.layout-sider {
  overflow: hidden;
  background: #fff !important;
  min-width: 249px !important;
  max-width: 249px !important;
  width: 249px !important;
  -webkit-flex: 0 0 249px !important;
      -ms-flex: 0 0 249px !important;
          flex: 0 0 249px !important;
}
.layout-sider .search-wrapper {
  padding: 0 10px;
}
.layout-sider .nav-api-list {
  overflow: hidden;
  padding: 10px 0;
  position: absolute;
  top: 96px;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.layout-sider .nav-api-list .dl {
  overflow: auto;
  height: 100%;
}
.layout-sider .nav-api-list .dl .ant-menu {
  height: 100%;
}
.layout-sider .nav-api-list .dl::-webkit-scrollbar {
  /*滚动条整体样式*/
  display: none;
}
.layout-nav-logo {
  height: 32px;
  margin: 16px 18px;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-items: center;
      -ms-flex-align: center;
          align-items: center;
  overflow: hidden;
  text-align: center;
}
.layout-nav-logo img {
  height: 70%;
}
.layout-nav-logo span {
  font-size: 20px;
  color: #333;
  display: inline-block;
  margin-left: 10px;
  opacity: 1;
  -webkit-transition: opacity 0.5s;
  -o-transition: opacity 0.5s;
  transition: opacity 0.5s;
}
.layout-nav-logo.collapsed {
  margin: 16px 29px;
}
.layout-nav-logo.collapsed span {
  opacity: 0;
  white-space: nowrap;
}



